<template>
  <div class="restore">
    <p class="restore__message" v-html="message" />
    <plain-input
      v-model="token"
      class="restore__token"
      code
      label="  "
      maxlength="4"
      inputmode="numeric"
      @input="
        $emit('post-token', { code: $event, phone: value, promocode }),
          $emit('typed')
      "
    />
    <div class="restore__actions">
      <plain-button
        :disabled="!!timer"
        :loading="isLoading"
        class="accept-btn"
        round
        large
        color="primary"
        @click="resendCode"
      >
        <span class="restore__resend-message">
            
          <span v-show="timer" class="restore__resend">
             {{ timer }} 
          </span>
        </span>
      </plain-button>
      <plain-button
        color="primary"
        flat
        @click.native="$emit('show-component', 'SignUp')"
      >
        
      </plain-button>
    </div>
  </div>
</template>

<script>
import PlainInput from 'common/components/PlainInput';
let interval;

export default {
  components: { PlainInput },
  props: {
    message: {
      type: String,
      default: ''
    },
    promocode: {
      type: String,
      default: ''
    },
    password: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      required: true
    },
    remained: {
      type: Number,
      default: 0
    },
    isLoading: Boolean
  },
  data() {
    return {
      token: '',
      isButtonDisabled: true,
      timer: 0
    };
  },
  watch: {
    remained() {
      this.timer = this.remained;
      this.tickTimer();
    }
  },
  mounted() {
    this.tickTimer();
    this.timer = this.remained;
  },
  methods: {
    tickTimer() {
      interval = setInterval(() => {
        (this.timer && this.timer--) || clearInterval(interval);
      }, 1000);
    },
    resendCode() {
      this.$emit('update:remained', 0);
      this.$emit('register', {
        phone: this.value,
        password: this.password,
        promocode: this.promocode
      });
    }
  }
};
</script>

