  margin-bottom: 18px;
  &__message {
    margin-bottom: 16px;
    font-size: 14px;
  }

  &__resend {
    display: block;
    color: #fff;
    font-size: 10px;
    line-height: 1;
  }
  &__actions {
    display: flex;
    align-items: center;
    margin-top: 8px;
  }
}
.restore__message {
  margin-bottom: 26px;
}
.accept-btn {
  margin: 0 16px 0 0;
  padding: 0 25px;
}
</style>
