<template>
  <form @submit.prevent="postNewPassword">
    <plain-input
      ref="newPasswordInput"
      v-model="newPassword"
      :error-text="errors.newPassword"
      type="password"
      label=" "
    />
    <plain-input
      ref="confirmNewPasswordInput"
      v-model="confirmNewPassword"
      :error-text="errors.confirmNewPassword"
      type="password"
      label="  "
    />
    <plain-button
      class="submit"
      :loading="isLoading"
      type="submit"
      color="primary"
      round
      large
    >
      
    </plain-button>
    <plain-button
      class="cancel"
      round
      flat
      color="primary"
      @click.native="$emit('show-component', 'restore')"
    >
      
    </plain-button>
  </form>
</template>

<script>
import PlainInput from 'common/components/PlainInput';

export default {
  components: { PlainInput },
  props: {
    restoreType: {
      type: String,
      required: true
    },
    value: {
      type: String,
      required: true
    },
    isLoading: Boolean
  },
  data() {
    return {
      newPassword: '',
      confirmNewPassword: ''
    };
  },
  computed: {
    errors() {
      const result = {
        newPassword: '',
        confirmNewPassword: ''
      };
      const { newPassword, confirmNewPassword } = this;
      if (!newPassword) result.newPassword = '   ';
      else if (newPassword.length < 6)
        result.newPassword = '   6 ';
      if (confirmNewPassword !== newPassword)
        result.confirmNewPassword = '  ';
      return result;
    },
    formIsValid() {
      return Object.values(this.errors).every(_ => !_);
    }
  },
  methods: {
    postNewPassword() {
      const { newPasswordInput, confirmNewPasswordInput } = this.$refs;
      newPasswordInput.isVisited = true;
      confirmNewPasswordInput.isVisited = true;
      const data = {
        type: this.restoreType,
        value: this.value,
        password: this.newPassword
      };
      if (this.formIsValid) this.$emit('set-new-password', data);
    }
  }
};
</script>

<style lang="scss" scoped>
.submit {
  margin: 6px 0;
}
.cancel {
  margin-left: 16px;
}
</style>
