<template>
  <dialog-window
    :show="showDialog"
    title="  MoreCargo"
    @close="$emit('update:showDialog', false)"
  >
    <component
      :is="currentComponent"
      v-bind="componentProps"
      :remained.sync="remained"
      :is-loading="isLoading"
      :incorrect-promo="incorrectPromo"
      :tryed-promocode="tryedPromocode"
      :is-loading-promocode="isLoadingPromocode"
      @register="registration"
      @post-promocode="postPromocode"
      @show-component="showComponent"
      @post-token="postToken"
      @set-type="setUserType"
      @typed="alert = {}"
      @signup-from-promo="tryedPromocode = true"
    />
    <transition name="fade">
      <alert
        v-if="Object.keys(alert).length"
        :message="alert.message"
        :type="alert.type"
      />
    </transition>
    <div
      slot="footer"
      :class="{ 'type-choice': currentComponent === 'TypeChoise' }"
      class="sign-up-footer"
    >
      <p>    MoreCargo?</p>
      <plain-button
        v-if="currentComponent === 'TypeChoise'"
        round
        outline
        large
        class="sign-in"
        @click="$emit('toggle-auth')"
      >
        
      </plain-button>
      <plain-button v-else flat color="primary" @click="$emit('toggle-auth')">
        
      </plain-button>
    </div>
  </dialog-window>
</template>

<script>
import { mapGetters } from 'vuex';
import Alert from 'common/components/Alert';
import axios from 'common/helpers/httpService';
import dialogWindow from 'common/components/Dialog';
import SignUp from './sign-up';
import TypeChoise from './type-choise';
import ConfirmPhone from './confirm-phone';

export default {
  components: {
    dialogWindow,
    SignUp,
    TypeChoise,
    Alert
  },
  props: {
    showDialog: Boolean,
    signUpFromPromo: String,
    signUpEmailPhone: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      currentComponent:
        this.signUpEmailPhone.phone || this.signUpFromPromo
          ? 'SignUp'
          : 'TypeChoise',
      componentProps:
        this.signUpEmailPhone.phone || this.signUpFromPromo
          ? {
              signUpEmailPhone: this.signUpEmailPhone,
              signUpFromPromo: this.signUpFromPromo,
              category: ''
            }
          : {
              category: ''
            },
      alert: {},
      serverError: false,
      remained: 0,
      isLoading: false,
      incorrectPromo: false,
      tryedPromocode: false,
      isLoadingPromocode: false
    };
  },
  computed: {
    ...mapGetters('errors', ['getError'])
  },
  created() {
    if (this.signUpFromPromo) {
      this.postPromocode({ code: this.signUpFromPromo });
    }
  },
  methods: {
    async registration({ phone, password, promocode }) {
      const commonData = `phone=${phone}&password=${password}`;
      const postData = this.signUpEmailPhone.phone
        ? `${commonData}&token=${this.signUpEmailPhone.token}&state=activation-stage-2`
        : `category=${this.componentProps.category ||
            'carrier'}&${commonData}&promo=${promocode}&state=registration-stage-1`;
      const postTopic = this.signUpEmailPhone.phone
        ? '/account/activate_code'
        : '/account/signup_code';
      this.isLoading = true;
      try {
        const { remained } = await axios.post(postTopic, postData);
        this.componentProps = {
          value: phone,
          promocode,
          password,
          message: `  <b>${phone}</b>      `
        };
        this.remained = remained;
        this.currentComponent = ConfirmPhone;
        this.alert = {};
      } catch ({ message: error }) {
        this.errorsFromServer({ error, type: 'user' });
      } finally {
        this.isLoading = false;
      }
    },
    async postPromocode({ code, phone = '' }) {
      if (!code) {
        this.tryedPromocode = false;
      }

      this.updateValidationStatus();

      if (code.length !== 6) return;

      this.isLoadingPromocode = true;
      try {
        await axios.post(
          '/account/signup_check_promo',
          `phone=${phone}&promo=${code}`
        );
        this.acceptedPromo();
      } catch ({ message: error }) {
        this.errorsFromServer({ error });
      } finally {
        this.tryedPromocode = true;
        this.isLoadingPromocode = false;
      }
    },
    async postToken({ code, phone, promocode }) {
      if (code.length !== 4) return;
      this.isLoading = true;
      const commonData = `phone=${phone}&code=${code}`;
      const postTopic = this.signUpEmailPhone.phone
        ? '/account/activate_match'
        : '/account/signup_match';
      const postData = this.signUpEmailPhone.phone
        ? `${commonData}&token=${this.signUpEmailPhone.token}&state=activation-stage-3`
        : `${commonData}&promo=${promocode}&state=registration-stage-2`;
      try {
        const data = await axios.post(postTopic, postData);
        if (this.signUpEmailPhone.phone) this.$emit('activate-new-user', data);
        else this.acceptedPhone(data);
      } catch ({ message: error }) {
        this.errorsFromServer({ error });
      } finally {
        this.isLoading = false;
      }
    },
    acceptedPhone(data) {
      this.$emit('finalise', data);
    },
    acceptedPromo() {
      this.incorrectPromo = false;
      this.alert = {
        type: 'successTransparent',
        message: ' '
      };
    },
    errorsFromServer({ error, type = null }) {
      this.alert = {
        type: 'dangerTransparent',
        message: this.getError({ error, type })
      };

      if (error === 'E_BADPROMO') {
        this.incorrectPromo = true;
      }

      this.serverError = true;
    },
    updateValidationStatus() {
      if (!this.serverError) {
        return;
      }

      this.alert = {};
      this.serverError = false;
    },
    setUserType(category) {
      this.componentProps.category = category;
      this.currentComponent = SignUp;
    },
    showComponent(current) {
      this.currentComponent = current;
      this.alert = {};
    }
  }
};
</script>

