<template>
  <transition name="small-fade">
    <div v-if="show" class="wrapper">
      <div :style="{ width: `${maxWidth}px` }" class="dialog">
        <header class="dialog__header">
          <div v-if="title" class="dialog__title">{{ title }}</div>
          <plain-button icon class="dialog__close" color="white" @click.stop="$emit('close')">
            <icon name="close" class="close-icon" />
          </plain-button>
        </header>
        <div :class="{ pb24px: hideFooter }" class="dialog__content">
          <slot>
            <p class="dialog__message">{{ message }}</p>
          </slot>
        </div>
        <footer v-show="!hideFooter" class="dialog__footer">
          <slot name="footer">
            <plain-button
              :loading="isLoading"
              round
              large
              color="primary"
              class="default-button"
              @click="$emit('accept')"
            >
              {{ buttonTitle }}
            </plain-button>
          </slot>
        </footer>
      </div>
    </div>
  </transition>
</template>

<script>
import PlainButton from './PlainButton.vue';
import Icon from './Icon.vue';

export default {
  name: 'Dialog',
  components: {
    PlainButton,
    Icon
  },
  props: {
    show: Boolean,
    message: String,
    isLoading: Boolean,
    title: {
      type: String,
      default: '!'
    },
    maxWidth: {
      type: String || Number,
      default: '408'
    },
    buttonTitle: {
      type: String,
      default: ' '
    },
    hideFooter: Boolean
  }
};
</script>

