<template>
  <header :class="{ transparent }" class="header">
    <div class="header__container">
      <div class="logo">
        <nuxt-link to="/" class="logo__link" aria-label="MoreCargo Logo">
          <icon name="logo" size="152" class="logo__img" />
        </nuxt-link>
      </div>
      <a
        :class="{ on: mobileIconActive }"
        href="javascript:void(0)"
        class="toggle-menu"
        aria-label="Toggle app menu"
        @click="toggleMobile"
      >
        <span />
      </a>
      <div
        :class="{ show: mobileIconActive }"
        class="action-wrap"
        @click="toggleMobile"
      >
        <nav class="navigation">
          <ul class="navigation__list">
            <li class="navigation__item">
              <nuxt-link to="/" class="navigation__link">
                 
              </nuxt-link>
            </li>
            <li class="navigation__item">
              <nuxt-link to="/carriers/" class="navigation__link">
                
              </nuxt-link>
            </li>
            <li class="navigation__item">
              <nuxt-link to="/drivers/" class="navigation__link">
                
              </nuxt-link>
            </li>
          </ul>
        </nav>
        <transition name="slideLeft">
          <div v-if="authorized !== null" class="actions">
            <plain-button
              v-if="authorized"
              class="md-raised gradient"
              @click="loginUser()"
            >
                
            </plain-button>
            <template v-else>
              <plain-button
                class="md-raised outline"
                @click="showDialog('SignIn')"
              >
                
              </plain-button>
              <plain-button
                class="md-raised gradient"
                @click="showDialog('SignUp')"
              >
                
              </plain-button>
            </template>
          </div>
        </transition>
      </div>
    </div>
    <sign-in-window
      v-if="showSignInDialog"
      :show-dialog="showSignInDialog"
      :show-new-password="showNewPassword"
      :email="email"
      @toggle-auth="toggleAuth"
      @login="loginUser"
      @set-new-password="loginUser"
      @updateRegistration="updateRegistration"
      @update-activation="updateActivation"
      @update:showDialog="
        setField({ field: 'showSignInDialog', value: $event })
      "
    />
    <sign-up-window
      v-if="showSignUpDialog"
      ref="signUp"
      :show-dialog="showSignUpDialog"
      :sign-up-email-phone="signUpEmailPhone"
      :sign-up-from-promo="signUpFromPromo"
      @toggle-auth="toggleAuth"
      @update:showDialog="
        setField({ field: 'showSignUpDialog', value: $event })
      "
      @finalise="showFinalPage"
      @activate-new-user="loginUser"
      @finalise-admin-registration="finaliseAdminRegistration"
    />
    <final-page
      v-if="showFinal"
      :phone="phone"
      :cookie="cookie"
      @registration-finished="loginUser"
    />
    <dialog-window
      message="   "
      max-width="300"
      :show="showErrorDialog"
      @close="onCloseErrorDialog"
      @accept="onCloseErrorDialog"
    />
  </header>
</template>

<script>
import { mapMutations, mapState, mapActions } from 'vuex';
import storageListener from 'common/helpers/storageListener';
import stringifyUrlParams from 'common/helpers/stringifyUrlParams';
import axios from 'common/helpers/httpService';
import DialogWindow from 'common/components/Dialog';
import Icon from 'common/components/Icon';
import SignInWindow from '@/components/auth-window/sign-in';
import SignUpWindow from '@/components/auth-window/sign-up';
import FinalPage from '@/components/auth-window/sign-up/final-page';

const appURL = process.env.VUE_APP_APP_URL;

export default {
  components: { SignInWindow, SignUpWindow, FinalPage, DialogWindow, Icon },
  props: {
    transparent: Boolean,
    mobileIconActive: Boolean
  },
  data() {
    return {
      showFinal: false,
      phone: '',
      email: '',
      cookie: '',
      signUpEmailPhone: {},
      signUpFromPromo: '',
      showNewPassword: false,
      showErrorDialog: false
    };
  },
  computed: {
    ...mapState(['showSignInDialog', 'showSignUpDialog']),
    ...mapState({
      loginData: state => state.login.fullData,
      authorized: state => state.login.authorized
    }),
    loginUri() {
      return stringifyUrlParams(this.loginData);
    }
  },
  watch: {
    mobileIconActive(val) {
      const isScrolled = window.pageYOffset !== 0;
      this.$emit(
        'update:transparent',
        !isScrolled && !val && this.$route.name !== 'drivers'
      );
    },
    showFinal(val) {
      if (val) document.documentElement.classList.add('hide-scroll');
      else document.documentElement.classList.remove('hide-scroll');
    }
  },
  async mounted() {
    storageListener({ login: this.login, logout: this.logout });
    const { query } = this.$route;
    try {
      if (query.signup_user_token) {
        const postData = `token=${query.signup_user_token}&state=restore-stage-1`;
        const data = await axios.post('/account/activate_phone', postData);
        this.setField({ field: 'showSignUpDialog', value: true });
        this.signUpEmailPhone = {
          phone: data.phone,
          token: query.signup_user_token
        };
      }

      if (query.restore_token) {
        // const postData = `token=${query.restore_token}&state=restore-stage-2`
        // await axios.post('/account/restore_match_token', postData)
        // this.email = query.email
        this.setField({ field: 'showSignInDialog', value: true });
        this.showNewPassword = true;
      }
    } catch (error) {
      if (error.message === 'E_UNKNOWNUSER') {
        this.showErrorDialog = true;
      }
      console.error(error);
    }

    if (query.ref) {
      this.signUpFromPromo = query.ref;
      this.setField({ field: 'showSignUpDialog', value: true });
    }

    if (query.show_login) {
      this.setField({ field: 'showSignInDialog', value: true });
    }

    if (query.show_signup) {
      this.setField({ field: 'showSignUpDialog', value: true });
    }

    if (query.logout) {
      localStorage.removeItem('sessionKey');
      this.$router.push('/');
    }

    try {
      const token = localStorage.getItem('sessionKey');
      if (token) {
        const data = await this.loginWithToken(token);
        if (data) this.showFinalPage(data);
      } else this.setLoginField({ field: 'authorized', value: false });
    } catch (error) {
      localStorage.removeItem('sessionKey');
      this.setLoginField({ field: 'authorized', value: false });
      console.error(error);
    }
  },
  methods: {
    ...mapMutations(['setField', 'toggleAuth', 'showDialog']),
    ...mapMutations('login', { setLoginField: 'setField' }),
    ...mapActions('login', ['loginWithToken', 'logout']),
    ...mapMutations('login', ['login']),
    finaliseAdminRegistration() {
      this.signUpFromAdmin = {};
      this.setField({ field: 'showSignUpDialog', value: false });
    },
    toggleMobile() {
      if (document.documentElement.clientWidth > 1080) return;
      if (this.mobileIconActive) {
        this.$emit('update:mobileIconActive', false);
      } else {
        this.$emit('update:mobileIconActive', true);
      }
    },
    showFinalPage(data) {
      this.login(data);
      this.phone = data.mqtt_login;
      this.cookie = data.cookie;
      this.setField({ field: 'showSignUpDialog', value: false });
      this.showFinal = true;
    },
    loginUser(data) {
      if (data) this.login(data);
      const loginData = data || this.loginData;
      const domain = window.location.hostname
        .split('.')
        .splice(-2)
        .join('.');
      Object.keys(loginData).forEach(_ => {
        if (_ === 'status') return;
        document.cookie = `${_}=${loginData[_]};domain=${domain};max-age=360;path=/`;
      });
      localStorage.setItem('sessionKey', loginData.cookie);
      window.location.assign(`${appURL}?token=${loginData.cookie}`);
    },
    updateRegistration(data) {
      this.setField({ field: 'showSignInDialog', value: false });
      this.showFinalPage(data);
    },
    updateActivation(data) {
      this.toggleAuth();
      this.signUpEmailPhone = { phone: data.phone };
      this.$nextTick(() => this.$refs.signUp.registration(data));
    },
    onCloseErrorDialog() {
      this.showErrorDialog = false;
      this.$router.push('/');
    }
  }
};
</script>

