<template>
  <div class="restore">
    <form @submit.prevent="emitRestore">
      <plain-input
        v-model="phone"
        v-mask="'+7 (###) ###-##-##'"
        label=""
        placeholder="+7 (___) ___-__-__"
        type="tel"
        :error-text="errors.phone"
        is-phone
        @input="$emit('typed')"
      />
    </form>
    <plain-button
      class="restore__submit"
      :disabled="isDisabled"
      :loading="isLoading"
      color="primary"
      round
      large
      @click="emitRestore"
    >
       
    </plain-button>
    <plain-button
      class="restore__cancel"
      flat
      color="primary"
      @click.native="$emit('show-component', 'SignIn')"
    >
      
    </plain-button>
  </div>
</template>

<script>
import { mask } from 'vue-the-mask';

export default {
  directives: { mask },
  props: {
    isLoading: Boolean
  },
  data: () => ({
    phone: ''
  }),
  computed: {
    isDisabled() {
      return this.errors.phone || !this.phone.length;
    },
    errors() {
      const phoneRegExp = /^((D)?(8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{4,15}$/;
      const result = {
        phone: ''
      };
      const { phone } = this;
      if (!phone) result.phone = '   ';
      else if (!phoneRegExp.test(phone)) result.phone = '  ';
      return result;
    }
  },
  methods: {
    emitRestore() {
      this.$emit('restore', { type: 'phone', value: this.phone });
    }
  }
};
</script>

<style lang="scss" scoped>
.restore {
  &__cancel {
    margin-left: 16px;
  }
  &__submit {
    margin: 6px 0;
  }
}
</style>
