<template>
  <form @submit.prevent="emitLogin">
    <plain-input
      ref="phoneInput"
      v-model="phone"
      v-mask="'+7 (###) ###-##-##'"
      label=""
      class="sign-in-form__phone"
      type="tel"
      placeholder="+7 (___) ___-__-__"
      inputmode="tel"
      is-phone
      @input="$emit('typed')"
    />
    <plain-input
      ref="passwordInput"
      v-model="password"
      type="password"
      class="password-field"
      label=""
      @input="$emit('typed')"
    />
    <plain-button
      flat
      color="primary"
      @click="$emit('show-component', 'restore')"
    >
        
    </plain-button>
    <plain-button
      class="sign-in-form__submit"
      :disabled="!formIsValid"
      :loading="isLoading"
      color="primary"
      type="submit"
      large
      round
    >
      
    </plain-button>
  </form>
</template>

<script>
import PlainInput from 'common/components/PlainInput';
import { mask } from 'vue-the-mask';

export default {
  name: 'SignIn',
  components: { PlainInput },
  directives: { mask },
  props: {
    isLoading: Boolean
  },
  data: () => ({
    phone: '',
    password: ''
  }),
  computed: {
    errors() {
      const result = {
        phone: '',
        password: ''
      };
      const { phone, password } = this;
      if (!phone) result.phone = '   ';
      else if (!/^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{4,15}$/.test(phone))
        result.phone = ' ';
      if (!password) result.password = '   ';
      return result;
    },
    formIsValid() {
      return Object.values(this.errors).every(_ => !_);
    }
  },
  methods: {
    emitLogin() {
      const { phoneInput, passwordInput } = this.$refs;
      phoneInput.isVisited = true;
      passwordInput.isVisited = true;
      const { phone, password } = this;
      if (this.formIsValid) this.$emit('login', { phone, password });
    }
  }
};
</script>

