    /deep/ input::-ms-input-placeholder {
      color: #111111 !important;
      opacity: 1;
    }

    /deep/ input:-ms-input-placeholder {
      color: #111111 !important;
      opacity: 1;
    }
  }

  &__submit {
    display: flex;
    margin: 16px 0;
  }
}

