<template>
  <form class="sign-up-form" @submit.prevent="emitRegister">
    <plain-input
      ref="phoneInput"
      v-model="phone"
      v-mask="'+7 (###) ###-##-##'"
      :disabled="!!signUpEmailPhone.phone"
      :error-text="errors.phone"
      label=""
      type="tel"
      placeholder="+7 (___) ___-__-__"
      class="sign-up-form__phone"
      is-phone
      @input="$emit('typed')"
    />
    <plain-input
      ref="passwordInput"
      v-model="password"
      :error-text="errors.password"
      type="password"
      label="   "
      class="password-field"
    />
    <div
      v-if="showPromoCode"
      :class="{ 'sign-up-form__promo-block--show': showPromo }"
      class="sign-up-form__promo-block"
    >
      <plain-input
        v-if="showPromo"
        ref="promocodeInput"
        :error-text="errors.promocode"
        :value="promocode"
        :disabled="(tryedPromocode && !incorrectPromo) || isLoadingPromocode"
        label=""
        maxlength="6"
        inputmode="numeric"
        @input="onPromoInput"
      />
      <icon
        v-if="tryedPromocode && promocode && !isLoadingPromocode"
        class="sign-up-form__promo-icon"
        :class="incorrectPromo ? 'error' : 'success'"
        size="22"
        :name="
          incorrectPromo ? 'alertCircleOutline' : 'checkboxMarkedCircleOutline'
        "
      />
      <PlainCircular
        v-if="isLoadingPromocode"
        width="22"
        height="22"
        class="promocode-spinner"
      />
    </div>
    <plain-button
      v-if="showPromoCode && !signUpFromPromo"
      flat
      color="primary"
      class="sign-up-form__promo"
      @click="onClickPromoBtn"
    >
        {{ showPromo ? ' ' : ' ' }}
    </plain-button>
    <plain-button
      :loading="isLoading"
      class="button-signUp"
      round
      large
      type="submit"
      color="primary"
    >
      
    </plain-button>
    <plain-button
      class="back-btn"
      color="primary"
      flat
      @click.native="$emit('show-component', 'TypeChoise')"
    >
      
    </plain-button>
  </form>
</template>

<script>
import PlainInput from 'common/components/PlainInput';
import PlainCircular from 'common/components/PlainCircular';
import Icon from 'common/components/Icon';
import { mask } from 'vue-the-mask';

export default {
  name: 'SignUp',
  components: { PlainInput, Icon, PlainCircular },
  directives: { mask },
  props: {
    signUpEmailPhone: {
      type: Object,
      default: () => ({})
    },
    signUpFromPromo: String,
    isLoading: Boolean,
    category: String,
    incorrectPromo: Boolean,
    tryedPromocode: Boolean,
    isLoadingPromocode: Boolean
  },
  data: () => ({
    showPromo: false,
    phone: '',
    password: '',
    promocode: ''
  }),
  computed: {
    isValid() {
      return Object.values(this.errors).every(_ => !_);
    },
    errors() {
      const result = {
        promocode: '',
        password: '',
        phone: ''
      };
      const { phone, password, promocode } = this;
      if (!phone) result.phone = '   ';
      else if (!/^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{4,15}$/.test(phone))
        result.phone = ' ';
      if (!password) result.password = '   ';
      else if (password.length < 6)
        result.password = '   6 ';
      if (promocode && promocode.length < 6)
        result.promocode = '   6 ';
      return result;
    },
    showPromoCode() {
      return !this.signUpEmailPhone.phone && this.category !== 'shipper';
    }
  },
  mounted() {
    if (this.signUpEmailPhone.phone) {
      this.phone = this.signUpEmailPhone.phone;
      document.querySelector('.sign-up-form__phone input').value = this.phone;
    }

    if (this.signUpFromPromo) {
      this.showPromo = true;
      this.promocode = this.signUpFromPromo;
      this.$emit('signup-from-promo');
    }
  },
  methods: {
    emitRegister() {
      const { phone, password, promocode } = this;
      const { phoneInput, passwordInput, promocodeInput } = this.$refs;
      phoneInput.isVisited = true;
      passwordInput.isVisited = true;
      if (promocodeInput) promocodeInput.isVisited = true;
      if (this.isValid) this.$emit('register', { phone, password, promocode });
    },
    onPromoInput(event) {
      this.promocode = event;
      this.$emit('post-promocode', { code: this.promocode, phone: this.phone });
    },
    onClickPromoBtn() {
      this.showPromo = !this.showPromo;

      if (!this.showPromo) {
        this.promocode = '';
        this.$emit('typed');
        this.$emit('post-promocode', {
          code: this.promocode,
          phone: this.phone
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/vars';
.button-signUp {
  margin: 6px 0 18px 0;
}

.back-btn {
  margin: 0 0 0 14px;
}
.password-field {
  margin-top: 6px;
}
.promocode-spinner {
  position: absolute;
  top: 32px;
  right: 10px;
  color: #009ce9;
}
.sign-up-form {
  &__promo {
    color: #009ce9;
    font-size: 14px;
    margin-bottom: 13px;
    display: block;
    cursor: pointer;
  }
  &__promo-block {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.25s ease;
  }
  &__promo-icon {
    position: absolute;
    right: 10px;
    top: 32px;

    &.error {
      color: #ff3535;
    }

    &.success {
      color: #65b38a;
    }
  }

  .sign-up-form__promo-block--show {
    height: 88px;
    padding-top: 6px;
  }
  &__promo {
    color: #009ce9;
    font-size: 14px;
    margin-bottom: 13px;
    display: block;
    cursor: pointer;
  }
  &__promo-block {
    height: 0;
    overflow: hidden;
    transition: height 0.25s ease;
  }
  &.sign-up-form--promo {
    height: 351px;
  }
}

@media screen and (max-width: breakpoint(sm)) {
  .password-field {
    margin-top: 3px;
  }

  .sign-up-form .sign-up-form__promo-block--show {
    padding-top: 3px;
  }
}
</style>
