<template>
  <div class="restore">
    <p class="restore__message" v-html="message" />
    <plain-input
      v-if="restoreType === 'phone'"
      ref="match_restore"
      v-model="token"
      class="restore__token"
      code
      label="  "
      maxlength="4"
      @input="
        $emit('post-token', { code: $event, phone: value }), $emit('typed')
      "
    />
    <div class="restore__actions">
      <plain-button
        :disabled="!!timer"
        color="primary"
        class="ml-0"
        round
        large
        @click="resendCode"
      >
        <span class="restore__resend-message">
           
          <span v-show="timer" class="restore__resend">
             {{ timer }} 
          </span>
        </span>
      </plain-button>
      <plain-button
        class="restore__cancel"
        color="primary"
        flat
        @click.native="$emit('show-component', 'restore')"
      >
        
      </plain-button>
    </div>
  </div>
</template>

<script>
let interval;

export default {
  props: {
    message: {
      type: String,
      default: ''
    },
    restoreType: {
      type: String,
      default: 'phone'
    },
    remained: {
      type: Number,
      default: 30
    },
    value: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      token: '',
      isButtonDisabled: true,
      timer: this.remained
    };
  },
  mounted() {
    this.tickTimer();
  },
  methods: {
    tickTimer() {
      interval = setInterval(() => {
        (this.timer && this.timer--) || clearInterval(interval);
      }, 1000);
    },
    resendCode() {
      this.timer = this.remained;
      this.tickTimer();
      this.$emit('restore', { type: this.restoreType, value: this.value });
    }
  }
};
</script>

<style lang="scss" scoped>
.restore {
  &__message {
    margin-bottom: 16px;
    font-size: 14px;
  }

  &__resend {
    display: block;
    color: #fff;
    font-size: 10px;
    line-height: 1;
  }
  &__actions {
    margin: 6px 0;
    display: flex;
    align-items: center;
  }
  &__cancel {
    margin-left: 16px;
  }
}
.restore__resend-message {
  font-size: 14px;
}
.restore__resend {
  margin-top: 8px;
  position: relative;
  top: -5px;
}
</style>
