<template>
  <div class="final-page">
    <div class="final-page__header">
      <icon name="logo" class="final-page__logo" />
    </div>
    <div class="final-page__wrapper">
      <div class="final-page-banner">
        <div class="final-page-banner__block">
          <h3 class="final-page-banner__title">
            !
          </h3>
          <h4 class="final-page-banner__subtitle">
                 MoreCargo
          </h4>
          <p class="final-page-banner__text">
                    ,
                  
             
          </p>
        </div>
        <img src="/img/8.png" class="man" />
      </div>
      <form class="final-page-fields" @submit.prevent="finalRegistaration">
        <div class="radio-group-label">
           ?
        </div>
        <div class="radios">
          <div class="radio-input">
            <input
              id="UL"
              v-model="radioGroup"
              type="radio"
              name="entity"
              value="UL"
            />
            <label for="UL">   </label>
          </div>
          <div class="radio-input">
            <input
              id="FL"
              v-model="radioGroup"
              type="radio"
              name="entity"
              value="FL"
            />
            <label for="FL"> </label>
          </div>
        </div>
        <plain-input
          :value="lastName"
          label=""
          :error-text="errors.lastName"
          name="lastname"
          class="field"
          @input="capitalize('lastName', $event)"
        />
        <plain-input
          :value="firstName"
          label=""
          :error-text="errors.firstName"
          name="name"
          class="field"
          @input="capitalize('firstName', $event)"
        />
        <template v-if="radioGroup === 'FL' && !isShipper">
          <plain-input
            :value="midName"
            :disabled="withoutPatronymic"
            :error-text="errors.midName"
            class="field"
            name="patronymic"
            label=""
            @input="capitalize('midName', $event)"
          />
          <div class="checkbox-input patronymic-checkbox">
            <input
              id="patronymic-checkbox"
              v-model="withoutPatronymic"
              type="checkbox"
              name="patronymic-checkbox"
              @change="toggleHasPatronymic"
            />
            <label for="patronymic-checkbox"> </label>
          </div>
        </template>
        <plain-input
          v-model="email"
          :error-text="errors.email"
          label="E-mail"
          class="field"
          type="email"
          name="email"
          required
        />
        <plain-autocomplete
          v-if="!isShipper"
          v-model="homeArea"
          :error-text="errors.homeArea"
          :options="states"
          label=" "
        />
        <div class="checkbox-input agreement">
          <input
            id="agreement"
            v-model="agreement"
            type="checkbox"
            name="agreement"
          />
          <label for="agreement">
             ,       
            <a href="/terms.pdf" target="_blank">
               
            </a>
              
            <a href="/rules.pdf" target="_blank"> </a>
            ,     
            <a href="/legal.pdf" target="_blank">
               
            </a>
                     
              
          </label>
        </div>
        <plain-button
          :disabled="buttonIsDisabled"
          :loading="isLoading"
          type="submit"
          class="to-cabinet"
          color="primary"
          round
          large
        >
             
        </plain-button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapGetters } from 'vuex';
import axios from 'common/helpers/httpService';
import PlainAutocomplete from 'common/components/PlainAutocomplete';
import PlainInput from 'common/components/PlainInput';
import Icon from 'common/components/Icon';
import states from 'common/helpers/states';

/* eslint-disable-next-line */
const checkMail = v => /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v)

export default {
  components: {
    PlainAutocomplete,
    PlainInput,
    Icon
  },
  props: {
    phone: {
      type: String,
      required: true
    },
    cookie: {
      type: String,
      required: true
    }
  },
  data: () => ({
    email: '',
    firstName: '',
    lastName: '',
    midName: '',
    withoutPatronymic: false,
    agreement: false,
    homeArea: '',
    radioGroup: '',
    isLoading: false
  }),
  computed: {
    ...mapGetters('login', ['isShipper']),
    formIsValid() {
      return Object.values(this.errors).every(_ => !_);
    },
    buttonIsDisabled() {
      return !this.formIsValid || !this.radioGroup || !this.agreement;
    },
    errors() {
      const result = {
        firstName: '',
        lastName: '',
        midName: '',
        email: '',
        homeArea: ''
      };
      const {
        firstName,
        lastName,
        midName,
        email,
        withoutPatronymic,
        homeArea,
        isShipper,
        radioGroup
      } = this;
      if (!firstName) result.firstName = '   ';
      if (!lastName) result.lastName = '   ';
      if (!midName && !withoutPatronymic && !isShipper && radioGroup === 'FL') {
        result.midName =
          '       " "';
      }
      if (!email) result.email = ' e-mail';
      else if (!checkMail(email)) result.email = 'E-mail ';
      if (!homeArea && !isShipper) result.homeArea = '  ';
      return result;
    }
  },
  created() {
    this.states = states;
  },
  methods: {
    ...mapActions('common', ['createController']),
    ...mapMutations(['setField']),
    toggleHasPatronymic() {
      if (this.withoutPatronymic) this.midName = '';
    },
    async finalRegistaration() {
      try {
        this.isLoading = true;
        let postData = `session=${this.cookie}&firstname=${this.firstName}&midname=${this.midName}&lastname=${this.lastName}&without_patronymic=${this.withoutPatronymic}&email=${this.email}&entity=${this.radioGroup}&agree=${this.agreement}&phone=${this.phone}&state=registration-step-3`;
        if (!this.isShipper) postData += `&home=${this.homeArea}`;
        const data = await axios.post('/account/signup/completion', postData);
        this.$emit('registration-finished', { ...data, cookie: this.cookie });
      } catch (error) {
        console.error(error);
        this.isLoading = false;
      }
    },
    capitalize(field, value) {
      const val = value.trim();
      this[field] = val ? val[0].toUpperCase() + val.slice(1) : '';
    }
  }
};
</script>
