<template>
  <dialog-window
    :show="showDialog"
    :title="title"
    :hide-footer="hideFooterModal"
    @close="$emit('update:showDialog', false)"
  >
    <component
      :is="currentComponent"
      v-bind="componentProps"
      :is-loading="isLoading"
      @show-component="setComponent"
      @restore="restore"
      @login="login"
      @post-token="postToken"
      @set-new-password="postNewPassword"
      @typed="alert = null"
    />
    <transition name="fade">
      <alert v-if="alert" :message="alert.message" :type="alert.type" />
    </transition>
    <div
      v-if="currentComponent === 'SignIn'"
      slot="footer"
      class="sign-in-footer"
    >
      <p class="sign-in-footer__text">
           MoreCargo?
      </p>
      <plain-button
        class="sign-in-footer__button"
        flat
        color="primary"
        @click="$emit('toggle-auth')"
      >
        
      </plain-button>
    </div>
  </dialog-window>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import Alert from 'common/components/Alert';
import axios from 'common/helpers/httpService';
import dialogWindow from 'common/components/Dialog';
import SignIn from './sign-in';
import Restore from './restore';
import Restore2 from './restore2';
import NewPassword from './new-password';

export default {
  components: {
    SignIn,
    Restore,
    Restore2,
    NewPassword,
    Alert,
    dialogWindow
  },
  props: {
    showDialog: {
      type: Boolean,
      default: true
    },
    showNewPassword: {
      type: Boolean,
      default: false
    },
    email: String
  },
  data() {
    return {
      currentComponent: this.showNewPassword ? 'NewPassword' : 'SignIn',
      componentProps: this.showNewPassword
        ? { restoreType: 'email', value: this.email }
        : {},
      alert: null,
      isLoading: false
    };
  },
  computed: {
    ...mapState(['defaultURL']),
    ...mapGetters('errors', ['getError']),
    title() {
      return `${
        ['restore', 'restore2', 'NewPassword'].includes(this.currentComponent)
          ? ' '
          : '  MoreCargo'
      }`;
    },
    hideFooterModal() {
      return ['restore', 'restore2', 'NewPassword'].includes(
        this.currentComponent
      );
    }
  },
  methods: {
    async restore({ type, value }) {
      const postData = `${type}=${value}&state=restore-stage-1`;
      this.isLoading = true;
      try {
        const { remained } = await axios.post(
          `/account/restore_${type}`,
          postData
        );
        const message =
          type === 'phone'
            ? `  <b>${value}</b>    c    `
            : `   <b>${value}</b>   c    `;
        this.componentProps = {
          restoreType: type,
          message,
          remained,
          value
        };
        this.currentComponent = 'restore2';
      } catch (error) {
        this.errorsFromServer(error, type);
      } finally {
        this.isLoading = false;
      }
    },
    async login({ phone, password }) {
      const clientId = `mqtt_${Math.random()
        .toString(16)
        .substr(2, 8)}`;
      this.isLoading = true;
      try {
        const data = await axios.post(
          '/account/login',
          `phone=${phone}&password=${password}&clientId=${clientId}`
        );
        const { phase } = data;
        if (phase === 'initial') return this.$emit('updateRegistration', data);
        this.$emit('login', { ...data, clientId });
      } catch (error) {
        if (error.message === 'E_ACTIVATION_IS_NOT_FINISHED') {
          this.$emit('update-activation', { phone, password });
        } else this.errorsFromServer(error, 'phone');
      } finally {
        this.isLoading = false;
      }
    },
    async postToken({ code, phone }) {
      if (code.length !== 4) return;
      const postData = `phone=${phone}&code=${code}&state=restore-stage-1`;
      try {
        await axios.post('/account/restore_match_code', postData);
        this.componentProps = { restoreType: 'phone', value: phone };
        this.currentComponent = 'NewPassword';
      } catch (error) {
        this.errorsFromServer(error);
      }
    },
    async postNewPassword({ type, value, password }) {
      const dataString = `${type}=${value}`;
      const url = `restore_password_${type}`;
      const postData = `${dataString}&password=${password}&state=restore-stage-3`;
      this.isLoading = true;
      try {
        const data = await axios.post(`/account/${url}`, postData);
        this.$emit('set-new-password', data);
      } catch (error) {
        this.errorsFromServer(error);
      } finally {
        this.isLoading = false;
      }
    },
    errorsFromServer(error, type = null) {
      this.alert = {
        type: 'dangerTransparent',
        message: this.getError({ error: error.message, type }),
        field: type
      };
    },
    setComponent(component) {
      this.currentComponent = component;
      this.alert = null;
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~common/styles/main';
@import '~@/assets/sass/vars';

.sign-in {
  &-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #8b8b8b;
    font-size: 14px;

    &__register {
      color: #009ce9;
      cursor: pointer;
    }

    &__text {
      display: inline;
      margin-right: 4px;
    }
    &__button {
      height: auto;
      margin: 0;
    }
  }

  .dialog__content {
    padding-bottom: 18px;
    overflow: hidden;
  }
}
</style>
