const stringify = (params, prefix = '', result = {}) => {
  if (Array.isArray(params)) {
    // eslint-disable-next-line
    result[prefix] = params;
  } else {
    Object.keys(params).forEach(key => {
      if (params[key] && typeof params[key] === 'object') {
        stringify(params[key], `${prefix}[${key}]`, result);
      } else {
        // eslint-disable-next-line
        result[`${prefix}[${key}]`] = params[key];
      }
    });
  }

  return result;
};

const stringifyRoot = params => {
  const result = {};
  Object.keys(params).forEach(key => {
    if (params[key] && typeof params[key] === 'object') {
      stringify(params[key], key, result);
    } else {
      result[key] = params[key];
    }
  });
  return Object.keys(result).reduce((acc, curr) => `${acc}${curr}=${result[curr]}&`, '?');
};

export default stringifyRoot;
