<template>
  <div class="autocomplete">
    <div class="wrapper" @click="showOptions = true">
      <plain-input
        :label="label"
        :error-text="errorText"
        v-model="inputValue"
        class="input"
        @focus="showOptions = true"
      />
      <i :class="{ 'is-up': showOptions }" aria-hidden="true" class="autocomplete__icon" />
    </div>
    <ul v-show="showOptions" class="options">
      <li v-for="(option, i) in filteredList" :key="i" @click="onOptionClick(option)">
        {{ option.fullname }}
      </li>
      <li v-if="!filteredList.length" class="empty"> </li>
    </ul>
  </div>
</template>

<script>
import PlainInput from 'common/components/PlainInput.vue';

export default {
  name: 'PlainAutocomplete',
  components: { PlainInput },
  props: {
    options: {
      type: Array,
      default: () => []
    },
    label: String,
    errorText: String
  },
  data: () => ({
    showOptions: false,
    inputValue: ''
  }),
  computed: {
    filteredList() {
      return this.options.filter(_ =>
        _.fullname.toLowerCase().includes(this.inputValue.toLowerCase())
      );
    }
  },
  mounted() {
    document.addEventListener('click', this.checkVisibility);
  },
  beforeDestroy() {
    document.removeEventListener('click', this.checkVisibility);
  },
  methods: {
    onOptionClick(option) {
      this.$emit('input', option.regioncode);
      this.showOptions = false;
      this.inputValue = option.fullname;
    },
    checkVisibility(e) {
      if (e.target.closest('.autocomplete')) return;
      const isSelected = this.options.find(_ => _.fullname === this.inputValue);
      if (!isSelected) this.inputValue = '';
      this.showOptions = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.autocomplete {
  position: relative;
  &__icon {
    position: absolute;
    right: 8px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 5px 0 5px;
    border-color: rgba(0, 0, 0, 0.54) transparent transparent transparent;
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
    &.is-up {
      transform: rotate(180deg);
      border-color: #009ce9 transparent transparent transparent;
    }
  }
  .plain-input--error + .autocomplete__icon {
    border-color: #ff3535 transparent transparent transparent;
  }
}

.wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.icon {
  position: absolute;
  right: 0;
}

.options {
  position: absolute;
  z-index: 3;
  left: 0;
  right: 0;
  top: calc(100% - 14px);
  max-height: 300px;
  overflow: auto;
  list-style: none;
  background-color: #fff;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14),
    0 3px 14px 2px rgba(0, 0, 0, 0.12);

  li {
    display: flex;
    align-items: center;
    min-height: 44px;
    font-size: 14px;
    padding: 0 16px;
    cursor: pointer;

    &:hover {
      background-color: #f6f6f6 !important;
    }

    &.empty {
      pointer-events: none;
      font-size: 0.9rem;
    }
  }
}
</style>
